.home {
    justify-content: center;
    margin-bottom: 3rem;
    width: 100%;
    margin-top: 28px;
    .slide {
        position: relative;
        .icon-left {
            position: relative;
            color: red;
            top: 178px;
            left: 20px;
            padding: 20px;
            background-color: white;
            border-radius: 52px;
            z-index: 1;
        }
        .slider-dot {
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            bottom: 40px;
            z-index: 2;
            display: flex;

            .slider-dot-item {
                background: white;
                width: 12px;
                height: 12px;
                border-radius: 50%;
                margin-right: 5px;
                opacity: 0.5;
            }
            .active {
                opacity: 1;
            }
        }
        .myslide {
            width: 100%;
            display: flex;
            overflow: hidden;
            position: relative;

            .slideItem {
                width: 100%;
                flex: 1 0 100%;
            }
        }
        .icon-right {
            position: relative;
            color: black;
            top: -178px;
            left: 1020px;
            padding: 20px;
            background-color: white;
            border-radius: 52px;
            z-index: 1;
        }
        margin-bottom: 3rem;
        img {
            width: 100%;
        }
    }

    .homeContent {
        .homeContentTitle {
            .h3 {
                margin: 10px 0 5px 0;
            }
        }
        .list-promotion {
            display: flex;
            .promotion-item {
                width: 35%;
                background: #ffff;
                display: block;
                border-radius: 4px;
                border: 1px solid black;
                margin: 0 5px;
                .promotion__inner {
                    padding: 6px;
                    width: 100%;
                    height: 140px;
                    // position: relative;
                    .contentPromotion_inner {
                        height: 9 0px;
                        span {
                            display: block;
                            font-weight: 700;
                            font-size: 16px;
                        }
                        p {
                            margin: 3px 0px !important;
                            font-size: 13px;
                        }
                    }
                    .coppyCode {
                        display: block;
                        text-align: right;
                        justify-content: right;
                        font-weight: 600;
                        // position: absolute;
                        // top: 71%;
                        // right: 10px;
                        .btnCoppyCode {
                            padding: 5px 15px;
                            border: 2px solid black;
                            border-radius: 15px;
                        }
                    }
                }
            }
        }
    }
}
